"use client";

import { useState, useEffect } from 'react';
import { ProductCard } from '@/components/product-card';
import { getAllProducts } from '@/lib/data';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import type { Product } from '@/lib/types';

export default function ProductsPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [sortedProducts, setSortedProducts] = useState<Product[]>([]);

  useEffect(() => {
    const allProducts = getAllProducts();
    setProducts(allProducts);
    setSortedProducts(allProducts); // Initially, sorted is the same as all
  }, []);

  const handleSortChange = (value: string) => {
    let sorted = [...products];
    switch (value) {
      case 'price-asc':
        sorted.sort((a, b) => a.price - b.price);
        break;
      case 'price-desc':
        sorted.sort((a, b) => b.price - a.price);
        break;
      case 'featured':
      default:
         // "Featured" can be random or based on a specific logic.
         // Here we'll shuffle them to simulate a "featured" sort.
        sorted = [...products].sort(() => 0.5 - Math.random());
        break;
    }
    setSortedProducts(sorted);
  };

  return (
    <div className="container py-8 md:py-12">
      <div className="flex flex-col md:flex-row items-center justify-between mb-8 gap-4">
        <h1 className="text-3xl md:text-4xl font-headline font-bold">All Products</h1>
        <div className="flex items-center gap-4">
          <Select onValueChange={handleSortChange}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Sort by" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="featured">Featured</SelectItem>
              <SelectItem value="price-asc">Price: Low to High</SelectItem>
              <SelectItem value="price-desc">Price: High to Low</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 md:gap-8">
        {sortedProducts.map((product) => (
          <ProductCard key={product.id} product={product} />
        ))}
      </div>
    </div>
  );
}
